@keyframes circleOtherScreen { 0% { transform: none; }
  50% { transform: scale(0.9); }
  100% { transform: scale(1.02); } }

@keyframes nudgeMouse { 0%, 30%, 60%, 90% { transform: translateY(0); }
  20%, 50%, 80% { transform: translateY(8px); } }

@-webkit-keyframes trackBallSlide { 0%, 28%, 29.99%, 30% { opacity: 1;
    transform: scale(1) translateY(-6px); }
  6% { opacity: 1;
    transform: scale(0.9) translateY(5px); }
  14% { opacity: 0;
    transform: scale(0.4) translateY(25px); }
  15%, 19% { opacity: 0;
    transform: scale(0.4) translateY(-6px); }
  36% { opacity: 1;
    transform: scale(0.9) translateY(5px); }
  44% { opacity: 0;
    transform: scale(0.4) translateY(25px); }
  45%, 49% { opacity: 0;
    transform: scale(0.4) translateY(-6px); }
  58%, 59.99%, 60% { opacity: 1;
    transform: scale(1) translateY(-6px); }
  66% { opacity: 1;
    transform: scale(0.9) translateY(5px); }
  74% { opacity: 0;
    transform: scale(0.4) translateY(25px); }
  75%, 79% { opacity: 0;
    transform: scale(0.4) translateY(-6px); }
  100%, 88% { opacity: 1;
    transform: scale(1) translateY(-6px); } }

